import { mapState } from "vuex";
export default {
  data() {
    return {
      arrNav: [
        //{ title: "首页", type: "index" },
        //{ title: "小V", type: "vGoods" },
        { title: "附近", type: "nearby" },
        { title: "购物车", type: "goodsCart" },
        { title: "订单", type: "orders" },
        { title: "个人", type: "mine" }
        //{ title: "资讯", type: "news" },
      ]
    };
  },
  props: {
    active: {
      type: String,
      default: "index"
    }
  },
  computed: {
    ...mapState(["openId"])
  },
  methods: {
    // 跳转大导航
    pageToBar(item, index) {
      if (item.type !== this.actBar) {
        if (item.type === "index") {
          this.$router.push({
            path: "/index",
          //  query: { open_id: this.openId }
          });
        }
        else if (item.type === "orders") {
          this.$router.push({
            path: "/store/orders",
            query: {
              //status: '[10,20,30,40,50,60]',
            }
          });
        }else if (item.type === "goodsCart") {
          this.$router.push({
            path: "/store/cart"
          });
        }else {
          this.$router.push("/" + item.type);
        }
      }
    }
  }
};



