
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
props: {
size: {
type: String,
default: 85
},
touch: {
type: String,
default: "none"
},
imageData: Array
},
directives: {
TransferDom
},
components: {
Previewer
},
computed: {
imageList() {

const imageData = this.imageData;
const imageList = [];
imageData.forEach(val => {
imageList.push({
src: val
});
});
return imageList;
}
},
methods: {

touchImage(index) {
if (this.touch === "delete") {
this.$emit("touchimg", index);
} else if (this.touch === "preview") {
this.$refs.previewer.show(index);
}
}
}
};



