
import XAddress from 'vux/src/components/x-address/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import value2name from 'vux/src/filters/value2name.js'

export default {
data() {
return {
cptIptCon: '',
hideAddress: [],
actAddress: [],
arrId:[],
chinaAddress: ChinaAddressV4Data
};
},
props: {
type: {
type: String,
default: "link"
},
title: String,
value: String,
iptCon: String || Number,
pro: String,
address: Array,
align: {
type: String,
default: "left"
},
link: String,
ricon: {
type: String,
default: "grey"
},
place: {
type: String,
default: "请输入关键字"
}
},
components: {
XAddress,Cell
},
computed: {
showAddress() {
const address = this.address;
if (address.length === 0) {
return "省 市 区";
} else {
return address[0] + " " + address[1] + " " + address[2];
}
}
},
watch: {
iptCon(newVal, oldVal) {
this.cptIptCon = newVal;
}
},
methods: {

changeIptCon() {
this.$emit("iptChange", {
pro: this.pro,
value: this.cptIptCon
});
},

changeRegion(arrId, arrTxt) {
this.hideAddress = arrTxt;
this.arrId = arrId;
},

actRegion(act) {
console.log(this.actAddress);
console.log(value2name(this.actAddress, ChinaAddressV4Data));
if (act) {

this.actAddress = this.hideAddress;



this.$emit("iptChange", {
pro: this.pro,
value: this.actAddress,
areaId: this.arrId.pop()
});
}
}
}
};




